@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans', sans-serif;
  padding: 0;
  margin: 0; }

select {
  font-family: 'Open Sans', sans-serif; }

.App {
  max-width: 1100px;
  margin: auto; }

.Nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  border-style: solid;
  border-width: 1px 0 1px 0;
  border-color: #ddd; }
  .Nav__item {
    padding: 10px; }
  .Nav select {
    background: #fff; }

.date-error {
  background: #ff8080;
  border-radius: 10px;
  padding: 10px; }

.Alert {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: #ddd; }
  .Alert__item {
    padding: 10px;
    -webkit-flex-grow: 1;
            flex-grow: 1; }

